'use client';

import React, { createContext, useContext, useState } from 'react';

type SpeedType = {
  download: string;
  upload: string;
};

type StorageType = {
  used: string;
  free: string;
};

type ConnectionStatus = 'connected' | 'disconnected' | 'connecting';

interface StatusContextType {
  downloads: number;
  setDownloads: React.Dispatch<React.SetStateAction<number>>;
  uploads: number;
  setUploads: React.Dispatch<React.SetStateAction<number>>;
  peers: number;
  setPeers: React.Dispatch<React.SetStateAction<number>>;
  speed: SpeedType;
  setSpeed: React.Dispatch<React.SetStateAction<SpeedType>>;
  storage: StorageType;
  setStorage: React.Dispatch<React.SetStateAction<StorageType>>;
  connectionStatus: ConnectionStatus;
  setConnectionStatus: React.Dispatch<React.SetStateAction<ConnectionStatus>>;
}

const StatusContext = createContext<StatusContextType | undefined>(undefined);

export const StatusProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [downloads, setDownloads] = useState<number>(0);
  const [uploads, setUploads] = useState<number>(0);
  const [peers, setPeers] = useState<number>(0);
  const [speed, setSpeed] = useState<SpeedType>({ download: '0 KB/s', upload: '0 KB/s' });
  const [storage, setStorage] = useState<StorageType>({ used: '0 GB', free: '0 GB' });
  const [connectionStatus, setConnectionStatus] = useState<ConnectionStatus>('connecting');

  return (
    <StatusContext.Provider value={{
      downloads, setDownloads,
      uploads, setUploads,
      peers, setPeers,
      speed, setSpeed,
      storage, setStorage,
      connectionStatus, setConnectionStatus
    }}>
      {children}
    </StatusContext.Provider>
  );
};

export const useStatus = () => {
  const context = useContext(StatusContext);
  if (!context) {
    throw new Error('useStatus must be used within a StatusProvider');
  }
  return context;
};
