'use client'
import React from 'react';
import { useStatus } from '../context/StatusContext'

const StatusBar: React.FC = () => {
  const {
    downloads,
    uploads,
    peers,
    speed,
    storage,
    connectionStatus
  } = useStatus();

  const getConnectionColor = () => {
    switch (connectionStatus) {
      case 'connected': return 'bg-green-500';
      case 'disconnected': return 'bg-red-500';
      case 'connecting': return 'bg-yellow-500';
      default: return 'bg-gray-500';
    }
  };

  return (
    <div className="fixed bottom-0 left-0 right-0 bg-gray-800 text-white p-2 text-xs flex justify-between items-center border-t border-gray-700">
      <div className="flex space-x-4">
        <div className="flex items-center">
          <span className="mr-1">↓:</span>
          <span>{downloads} active</span>
          <span className="ml-2">{speed.download}</span>
        </div>
        <div className="flex items-center">
          <span className="mr-1">↑:</span>
          <span>{uploads} active</span>
          <span className="ml-2">{speed.upload}</span>
        </div>
        <div>
          <span>Peers: {peers}</span>
        </div>
      </div>
      
      <div className="flex items-center space-x-4">
        <div>
          <span>Storage: {storage.used} / {storage.free}</span>
        </div>
        <div className="flex items-center">
          <span className="mr-2">Status:</span>
          <span className={`h-2 w-2 rounded-full ${getConnectionColor()}`}></span>
          <span className="ml-1 capitalize">{connectionStatus}</span>
        </div>
      </div>
    </div>
  );
};

export default StatusBar;